import type { Metadata } from "next";
import "./globals.css";
import "./styles/admin/all.css";
import AdminBar from "@/public/components/adminbar/page";
import React from "react";
import TopBar from "@/public/components/topbar/page";
import { createTheme, CssBaseline, IconButton, ThemeProvider } from "@mui/material";
import { Brightness4, Brightness7 } from "@mui/icons-material";
// import loading from "./loading";
// import AdminBar from "/components/adminbar/page";

export const metadata: Metadata = {
  title: "Web Developer",
  description: "Web Developer",
};
export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const [mode, setMode] = React.useState<"light" | "dark">("light");
  React.useEffect(() => {
    const savedMode = localStorage.getItem("theme");
    if (savedMode) {
      setMode(savedMode as "light" | "dark");
    }
  }, []);

  // Lưu trạng thái theme vào localStorage
  React.useEffect(() => {
    if (mode) {
      localStorage.setItem("theme", mode);
    }
  }, [mode]);

  // Tạo theme động dựa trên trạng thái mode
  const theme = createTheme({
    palette: {
      mode: mode, // "light" hoặc "dark"
    },
  });
  return (
    <html lang="en">
      <ThemeProvider theme={theme}>
      <CssBaseline /> {/* Đảm bảo sử dụng css chuẩn của MUI */}
      <div>
        <IconButton
          onClick={() => setMode(mode === "light" ? "dark" : "light")}
          color="inherit"
        >
          {mode === "light" ? <Brightness4 /> : <Brightness7 />}
        </IconButton>
      </div>
      <Component {...pageProps} />
    </ThemeProvider>

      

    </html>
  );
}
